@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0; }

body {
  background-color: #e2b946;
  font-family: "Source Code Pro"; }

.container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 150px 150px 150px 150px;
  justify-items: center;
  align-items: center; }

.title {
  font-size: 45px;
  grid-column: 1/7; }

.timer-container {
  position: relative;
  grid-column: 1/7;
  text-align: center;
  font-size: 25px; }

svg {
  position: absolute;
  top: -40px;
  right: -40px;
  width: 330px;
  height: 330px;
  -webkit-transform: rotateY(-180deg) rotateZ(-90deg);
          transform: rotateY(-180deg) rotateZ(-90deg); }

svg circle {
  stroke-dasharray: 1003px;
  stroke-dashoffset: 0px;
  stroke-linecap: round;
  stroke-width: 8px;
  stroke: black;
  fill: none;
  transition: 1s; }

.break-control {
  align-self: baseline;
  justify-self: end;
  grid-column: 2/4; }

.session-control {
  align-self: baseline;
  justify-self: start;
  grid-column: 4/6; }

.btn {
  width: 60px;
  cursor: pointer;
  z-index: 100; }

#start_stop {
  grid-column: 3/4;
  justify-self: flex-end; }

#reset {
  grid-column: 4/5;
  justify-self: flex-start; }

.control-grid {
  display: grid;
  width: 250px;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  justify-items: center;
  align-items: center; }

@keyframes colorchange {
  0% {
    background: #e2b946;
    color: black; }
  100% {
    background: #d81f00;
    color: white; } }

@-webkit-keyframes colorchange {
  0% {
    background: #e2b946;
    color: black; }
  100% {
    background: #d81f00;
    color: white; } }

@keyframes colorback {
  0% {
    background: #d81f00;
    color: white; }
  100% {
    background: #e2b946;
    color: back; } }

@-webkit-keyframes colorback {
  0% {
    background: #d81f00;
    color: white; }
  100% {
    background: #e2b946;
    color: back; } }

@keyframes countdown {
  from {
    stroke-dashoffset: 0px; }
  to {
    stroke-dashoffset: 1003px; } }

@-webkit-keyframes countdown {
  from {
    stroke-dashoffset: 0px; }
  to {
    stroke-dashoffset: 1003px; } }

